"use client"
import { useEffect, useState } from "react"
import { supabase } from "../../../lib/supabaseClient"
import { LineChart, Line, XAxis, YAxis, Tooltip, CartesianGrid, ResponsiveContainer } from "recharts"
import { Card, CardContent } from "../../../components/ui/card"

export default function DashboardPage() {
  const [iraData, setIraData] = useState([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    fetchIRA()
  }, [])

  async function fetchIRA() {
    setLoading(true)
    try {
      const { data, error } = await supabase
        .from("trial_balance")
        .select("account_name, debit, credit, year")

      if (error) throw error

      // üß© Define possible IRA/NTA account name keywords
      const iraKeywords = [
        "share from internal revenue collections", // exact barangay IRA name
        "ira",
        "internal revenue",
        "nta",
        "national tax",
      ]

      // üß† Filter IRA/NTA entries based on keywords
      const iraEntries = data.filter(row =>
        iraKeywords.some(keyword =>
          row.account_name?.toLowerCase().includes(keyword)
        )
      )

      // üßæ Sum total IRA by year
      const yearlyTotals = {}
      iraEntries.forEach(row => {
        // ensure year is number
        const year = Number(row.year)
        if (!year || isNaN(year)) return

        const amount = (parseFloat(row.debit) || 0) - (parseFloat(row.credit) || 0)
        yearlyTotals[year] = (yearlyTotals[year] || 0) + amount
      })

      // ‚úÖ Sort years numerically (so 2024 won‚Äôt be skipped)
      const sortedYears = Object.keys(yearlyTotals)
        .map(y => Number(y))
        .sort((a, b) => a - b)

      // üßÆ Format data for the chart
      const formatted = sortedYears.map(year => ({
        year,
        ira: yearlyTotals[year],
      }))

      setIraData(formatted)
    } catch (err) {
      console.error("Error fetching IRA data:", err)
    } finally {
      setLoading(false)
    }
  }

  if (loading) return <p>Loading IRA data...</p>

  // üßÆ Derived stats
  const lastYear = iraData[iraData.length - 1]?.year || "N/A"
  const lastIra = iraData[iraData.length - 1]?.ira || 0
  const prevIra = iraData[iraData.length - 2]?.ira || 0
  const growthRate = prevIra ? (((lastIra - prevIra) / prevIra) * 100).toFixed(2) : "0"

  return (
    <div className="space-y-6 p-6">
      {/* üè∑ Header */}
      <div>
        <h2 className="text-2xl font-bold text-gray-800">IRA / NTA Overview</h2>
        <p className="text-sm text-gray-500">
          Historical trend and yearly growth based on ‚ÄúShare from Internal Revenue Collections‚Äù
        </p>
      </div>

      {/* üìä KPI / Observations */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <Card>
          <CardContent className="p-4">
            <h2 className="text-sm text-gray-500">Latest IRA ({lastYear})</h2>
            <p className="text-2xl font-bold text-indigo-600">
              ‚Ç±{lastIra.toLocaleString()}
            </p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <h2 className="text-sm text-gray-500">Previous Year</h2>
            <p className="text-2xl font-bold">
              ‚Ç±{prevIra.toLocaleString()}
            </p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <h2 className="text-sm text-gray-500">Growth Rate</h2>
            <p
              className={`text-2xl font-bold ${
                growthRate >= 0 ? "text-green-600" : "text-red-600"
              }`}
            >
              {growthRate}%
            </p>
          </CardContent>
        </Card>
      </div>

      {/* üìà IRA Trend Chart */}
      <div className="bg-white rounded-xl shadow p-4 h-96">
        <h2 className="text-lg font-semibold mb-4">
          IRA / NTA Trend ({iraData[0]?.year}‚Äì{lastYear})
        </h2>
        <ResponsiveContainer width="100%" height="100%">
          <LineChart data={iraData}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="year" />
            <YAxis />
            <Tooltip
              formatter={(value) =>
                new Intl.NumberFormat("en-PH", {
                  style: "currency",
                  currency: "PHP",
                }).format(value)
              }
            />
            <Line type="monotone" dataKey="ira" stroke="#4F46E5" strokeWidth={3} />
          </LineChart>
        </ResponsiveContainer>
      </div>
    </div>
  )
}
